<template>
  <div id="footer-bar" class="animated fadeIn">
    <mt-tabbar class="zdcj-tabbar" v-model="selectedTab" fixed>
      <mt-tab-item id="时讯" href="#/headlines">
        <img slot="icon" src="../assets/nav-news.svg">
        时讯
      </mt-tab-item>
      <mt-tab-item id="行情">
        <img slot="icon" src="../assets/nav-markets.svg">
        行情
      </mt-tab-item>
      <mt-tab-item id="日历">
        <img slot="icon" src="../assets/nav-calendar.svg">
        日历
      </mt-tab-item>
      <mt-tab-item id="我的" href="#/user">
        <img slot="icon" src="../assets/nav-user.svg">
        我的
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>

export default {
  data () {
    return {
      selectedTab: '我的'
    }
  },

  methods: {

  },

  created(){
    
  }
}
</script>

<style lang="scss">
  @import "../style/mixin";
  .zdcj-tabbar {
    background: none;
    @include nav-style;
  }
</style>


